<template>
  <div class="page" id="firmtop">
    <div class="box search-bar">
      <div class="select">
        {{ in_name }}
        <select name="" v-model="in_id" @change="reset();pullList()">
          <option value="">全部</option>
          <option :value="industry.in_id" v-for="industry in industrys" :key="industry.in_id">{{ industry.in_name }}
          </option>
        </select>
      </div>
      <van-search class="bf1" placeholder="搜索" @change="reset();pullList()" v-model="company"/>
    </div>
    <div class="content bgc-white pl15">
      <van-list
              v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load="pullList">
        <a :href="'#/firmtop-detail?id=' + delivery.firm_id" class="box pt15 pb15 px-b mb15"
           v-for="delivery in deliverys" :key="delivery.firm_id">
          <img v-lazy="delivery.imgUrl.op_spic" width="100" height="100" alt="" class="banner">
          <div class="pl10 pr10 f12 bf1">
            <div class="f14">{{ delivery.firm_name }}</div>
            <div class="gray mt5 text-overflow-line2">{{ delivery.firm_lead }}</div>
            <div class="tag mt5 mb5">{{ delivery.in_name }}</div>
            <div><img src="@/assets/images/industryUnion/address.png" width="10" alt=""><span class="ml5">{{ delivery.f_address }}</span>
            </div>
          </div>
        </a>
      </van-list>
      <div v-show="finished && deliverys.length > 0" class="gray tc">没有更多了</div>
      <div v-show="deliverys.length == 0" class="gray tc mt15">没有数据</div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'firmtop',
    data() {
      return {
        finished: false,
        loading: false,
        page: 1,
        pageSize: 10,
        company: '',
        in_id: '',
        industrys: [],
        deliverys: []
      }
    },
    created() {
      this.getIndustry()
    },
    computed: {
      in_name() {
        let item = this.industrys.find((v) => {
          return v.in_id == this.in_id
        }) || {}
        return item.in_name || '请选择行业'
      }
    },
    methods: {
      reset() {
        this.page = 1
        this.deliverys = []
      },
      getIndustry() {
        this.axios.get(this.apiConfig.getIndustry).then(res => {
          if (res.s === 1) {
            this.industrys = res.d
          }
        })
      },
      pullList() {
        let {page, pageSize, in_id, company} = this
        this.axios.post(this.apiConfig.getDelivery, {
          page,
          pageSize,
          in_id,
          company
        }).then(res => {
          if (res.s === 1) {
            this.$store.dispatch('UPDATE_LOADINGSTATE', false)
            this.deliverys = res.d.data
            this.page++
            this.loading = false
            if (this.deliverys.length >= res.d.total) {
              this.finished = true
            }
          }
        })
      }
    }
  }
</script>
<style lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  #firmtop {
    .van-field__control {
      line-height: rem(26px);
      font-size: rem(14px);
    }
    .van-cell {
      padding: rem(3px) rem(10px);
    }
    .van-search {
      padding: 0 rem(15px) 0 rem(8px);
    }
  }
</style>
<style scoped lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  .banner {
    width: rem(100px);
    height: rem(100px);
    display: block;
  }
  .tag {
    background-color: #f6f6f6;
    color: #8a8c89;
    border-radius: 4px;
    padding: rem(3px) rem(10px);
    font-size: rem(12px);
    display: inline-block;
  }

  .search-bar {
    background-color: #f2f2f2;
    position: relative;
    select {
      position: absolute;
      top: 0;
      width: 100%;
      left: 0;
      height: 100%;
      opacity: 0;
    }
  }

  .select {
    &:after {
      position: absolute;
      display: inline-block;
      content: '';
      border-width: 4px;
      border-style: solid;
      border-color: #999 transparent transparent transparent;
      right: rem(5px);
      top: rem(12px);
    }
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: rem(120px);
    border-radius: 4px;
    background-color: #fff;
    font-size: rem(14px);
    padding: 0 rem(20px) 0 rem(15px);
    margin: rem(10px) 0 rem(10px) rem(18px);
    line-height: rem(30px);
  }
</style>
